.about-container{
    padding-top: 220px;
    display: flex;
    background-color: black;
    color: white;
    height: 100vh;
    margin-bottom: 0;
}

.about-desc{
    display: flex;
    flex-direction:column;
    justify-content: center;
    width: 100%;
    padding: 0 100px 0 100px;
}

.about-desc h3{
    margin-bottom: 8px;
    font-size: 32px;
}

.about-desc p{
    line-height: 1.3;
    font-size: 20px;
}

.about-img {
    width: 100%;
    height: 400px;
    display: flex;
    align-self: center;
    overflow: hidden;
    justify-content: center;
}


@media only screen and (max-width: 960px) {
    .about-container{
        width: 100%;
        flex-direction: column;
        padding-top: 170px;
        height: auto;
    }

    .about-desc {
        width: 90%;
        margin: 0 auto;
        padding: 40px 20px 50px 20px;
    }

    .about-desc h3 {
        font-size: 20px;
        text-align: center;
    }

    .about-desc p {
        font-size: 14px;
        text-align: center;
        flex-direction: column;
        
    }

    .about-img {
        width: 100%;
        margin-bottom: 50px;
    }

    .about-img img {
        width: 50%;
        margin-left: 22%;
    }
}
