@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cinzel&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Cinzel', serif;
}

.logoDc img{
    height: 220px;
    line-height: 80px;
    color: white;
}


nav{
    position: fixed;
    top: 0;
    font-size: 20px;
    width: 100%;
    background-color: rgb(57, 2, 94);
    padding: 0;
    display: flex;
    justify-content: space-between;
    height: 60px;
}

.item{
    display: flex;
    width: 50%;
    justify-content: space-around;
    color: aliceblue;
    text-decoration: none;
    padding: 0 1rem;
}

.item:hover{
    background-color: rgb(153, 119, 175);;
    color: black;
    transition: ease-in .4s;
    height: 22px;
}

.home {
    color: rgb(57, 2, 94);;
}

