/*---------------PRUEBA--------------*/

  .jobs-contenedor {
    padding-top: 120px;
    display: flex;
    background-color: black;
    height: 100vh;
  }
  
  .jobs {
    box-shadow: 3px 3px 3px #eaeaea;
    width: 25rem;
    margin: 10rem 1rem;
  }
  
  .jobs-titulo {
    background-color: blueviolet;
    color: aliceblue;
    text-align: center;
    padding: 1rem;
  }

  .jobs-info{
    background-color: rgb(217, 182, 250);
    padding: 1rem;
    border-bottom: 2px solid #eaeaea;
    text-align: center;
    color: #fff;
  }
  
  .jobs-btn{
    background-color: rgb(188, 123, 248);
    text-align: center;
    padding: 1rem;
    
  }

  .job__section {
      background-color: #000;
      height: 100vh;
  }

  .detalls {
      padding-top: 180px;
      font-size: 42px;
      color: white;
      color: aliceblue;
      text-align: center;
  }

  .cargo, .anio , .funciones {
      text-align: center;
      font-size: 22px;
      color: white;
  }

  .funciones {
      padding: 30px 200px 10px 200px;
  }

